  box-shadow: 0.05rem 0.1rem 20px  rgba(0, 0, 0, 0.2);

}
.imageContainer > img {
  object-fit: contain;
  width: 250px;
  height: 250px;
  max-width: 300px;
  margin-left: auto;
  margin-right: auto;
  max-height: 270px;
  display: block;
  transform: scale(1);
  opacity: 0.9;
  -webkit-transform: scale(1);
  transform: scale(1);
  transition: 0.2s ease-in-out;
}

.imageContainer:hover > img {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
  overflow: hidden;
  opacity: 1;
}

